import type { Handler, HandlerEvent, HandlerContext } from '@netlify/functions'
import { customAlphabet } from 'nanoid'
import { eq } from 'drizzle-orm'
import * as jose from 'jose'
import bcrypt from 'bcrypt'
import { users } from '../db/schema'
import { db } from '../db/db'
const secret = process.env.JWT_SECRET

const handler: Handler = async (event: HandlerEvent, context: HandlerContext) => {
  console.log('hit')
  const { name, email, password }: { name: string; email: string; password: string } = JSON.parse(
    event.body!
  )

  const nanoid = customAlphabet(
    '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz',
    15
  )

  try {
    // const existingUser = await db.query.users.findFirst({
    //   where: eq(users.id, 1)
    // })

    // returns an empty array if user is not found...
    const existingUser = await db.select().from(users).where(eq(users.email, email))

    if (existingUser.length > 0) {
      return {
        statusCode: 400,
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,HEAD,POST,OPTIONS',
          'Access-Control-Max-Age': '86400'
        },
        body: JSON.stringify({ success: false, message: 'User already exists' })
      }
    }

    const salt = await bcrypt.genSalt(10)
    const hashedPassword = await bcrypt.hash(password, salt)

    const userId = nanoid()
    const user = await db.insert(users).values({
      name: name,
      email: email,
      password: hashedPassword,
      userAuthId: nanoid(),
      userId: userId
    })

    // TODO jwt in cookies

    return {
      statusCode: 200,
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET,HEAD,POST,OPTIONS',
        'Access-Control-Max-Age': '86400'
      },
      body: JSON.stringify({ success: true, data: { user } })
    }
  } catch (error) {
    console.log('error while adding user', error)
    return {
      statusCode: 500,
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET,HEAD,POST,OPTIONS',
        'Access-Control-Max-Age': '86400'
      },
      body: JSON.stringify({ success: false, data: null })
    }
  }
}

export { handler }
